import React from 'react';
import styles from './Sidebar.scss';
import classNames from 'classnames/bind';
import SideBarWrapper from 'components/common/SideBarWrapper';
import { Link } from 'react-router-dom';
import Button from 'components/common/Button';
const cx = classNames.bind(styles);


const MenuItem = ({to, children, onClick}) => {
  return (<Link onClick={onClick} className={cx('menu-item')} to={to}>{children}</Link>)
}

const ClickableMenuItem = ({children ,onClick}) => {
  return (<div onClick={onClick} className={cx('menu-item')}>{children}</div>)
}

const Sidebar = ({visible, onClose, adminLogged, adminLogout, memberLogged, memberLogout, goToCartPage}) => (
  <SideBarWrapper visible={visible}>
  <div className={cx('top-menu')}>
    <div className={cx('description')}>
         :)
    </div>
  </div>
    <div className={cx('menu')}>
    {/* {
      (memberLogged) ? [<ClickableMenuItem onClick={memberLogout} key="adminLogout"></ClickableMenuItem>,
      <MenuItem to="/checkout" onClick={onClose} key="checkout"></MenuItem>,] 
      : !adminLogged &&
      [<MenuItem to="/login" onClick={onClose} key="login"></MenuItem>,
      <MenuItem to="/register" onClick={onClose} key="register"></MenuItem>]
    }
    {
      adminLogged && [
        <MenuItem to="/admin/main" onClick={onClose} key="main"> </MenuItem>,
        <MenuItem to="/editor" onClick={onClose} key="editor"> </MenuItem>,
        <MenuItem to="/admin/list" onClick={onClose} key="list">  </MenuItem>,
        <ClickableMenuItem onClick={adminLogout} key="adminLogout"> </ClickableMenuItem>
      ]
    } */}
    {
      !adminLogged && 
      [
          <MenuItem to="/item" onClick={onClose} key="item">
            
          </MenuItem>,
          <MenuItem to="/cart" onClick={onClose} key="cart">
            
          </MenuItem>,
          <MenuItem to="/qna" onClick={onClose} key="qnabbs">
             
          </MenuItem>
      ]
    }
    {
        (!memberLogged && !adminLogged) && 
        <MenuItem to="/login" onClick={onClose}>
          /
        </MenuItem>
    }
    {
      memberLogged ? 
      adminLogged ? null : [
        <ClickableMenuItem onClick={memberLogout} key="logout">
        
        </ClickableMenuItem>,
        <MenuItem to="/member" onClick={onClose} key="my-info">
           
        </MenuItem>,
        <MenuItem to="/checkout" onClick={onClose} key="checkout-info">
           
        </MenuItem>
      ] : 
      null
    }
    {
      adminLogged && [
        <MenuItem to="/admin/main" onClick={onClose} key="adminmain"></MenuItem>,
        <MenuItem to="/item" onClick={onClose} key="item">
            
          </MenuItem>,
          <MenuItem to="/cart" onClick={onClose} key="cart">
            
          </MenuItem>,
          <MenuItem to="/qna" onClick={onClose} key="qnabbs">
             
          </MenuItem>
      ]
    }
    </div>
  </SideBarWrapper>
);

export default Sidebar;


